* {
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

body {
  padding: 16px;
}

#app {
  max-width: 580px;
  margin: 64px auto;
  padding: 16px;
  background: #0d6058;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0);

  form {
    display: flex;
    gap: 8px;
    align-items: center;

    input {
      width: 100%;
      padding: 12px 16px;
      border-radius: 8px;
      border: none;
    }

    button {
      padding: 12px 16px;
      border-radius: 8px;
      border: none;
      color: #ffff;
      background:#18988b;
      cursor: pointer;

      &:hover {
      background:#169084;
    }
    }

    
  }
}